let didNotify = false;
let isNotifying = false;

$('#product_notify_form').submit(function(e){
  e.preventDefault();

  if (isNotifying === false && didNotify === false) {
    isNotifying = true;
    $('.notify-btn-text').text( "Signing you up..." );

    $.ajax({
      url:'https://hooks.zapier.com/hooks/catch/6311450/o10k5kr/',
      type:'post',
      data:$('#product_notify_form').serialize(),
      error:function() {
        isNotifying = false;
        didNotify = false;

        $('.notify-btn').css( "background", "darksalmon" );
        $('.notify-btn-text').text( "Please click to try again" );
      },
      success:function(){
        isNotifying = false;
        didNotify = true;

        $('.notify-btn').css( "background", "#43b943" );
        $(".notify-btn").hover(function() {
          $(this).css("padding-right","20px")
        });
        $('.notify-btn-text').text( "Thanks for signing up!" );
      },
    });
  }
});
