const API_URL = 'https://geolocation-db.com/json/0f761a30-fe14-11e9-b59f-e53803842572';
//const MS_MODAL_TIMEOUT = 90000; TODO reenable this
const MS_MODAL_TIMEOUT = 90000;

export async function getGeo() {
  const response = await fetch(API_URL);
  const geoData = await response.json();

  window.__shopper__ = window.__shopper__ || {};
  window.__shopper__.geo = geoData;

  return geoData;
}

export function setRegionalPopup(geoData) {
  let countryCode = geoData.country_code;

  function openGeoModal() {
    document.getElementById('geoModal').style.display = 'block';
    sessionStorage.setItem('sheertex.hasDisplayedGeoPopup','true');
  }

  window.__shopper__ = window.__shopper__ || {};
  window.__shopper__.countryCallback = () => {
    $('#country').countrySelect('setCountry', window.__shopper__.geo.country_name);
  };

  if (countryCode === 'US' || countryCode === 'CA') {
    //Do nothing
  } else{

    if (sessionStorage.getItem('sheertex.hasDisplayedGeoPopup') !== 'true') {
      $('#country').countrySelect();

      $(document).ready(function(){
        setTimeout(function() {
          $('#country').countrySelect('setCountry', geoData.country_name);
          openGeoModal();
        }, MS_MODAL_TIMEOUT);
      });
    }
  }
}

$('#geo_modal_form').submit(function(e){
  e.preventDefault();
  $.ajax({
    url:'https://hooks.zapier.com/hooks/catch/6311450/omol8sh/',
    type:'post',
    data:$('#geo_modal_form').serialize(),
    success:function(){
      // Redirect to another success page
      $('.inner-content').hide();
      $('.success').show();
    },
  });
});

function closeModal() {
  document.getElementById('geoModal').style.display = 'none';
  window.__shopper__ && window.__shopper__.popupCallback == null;
}

// When the user clicks anywhere outside of the modal, close it
window.onclick = function(event) {
  if (event.target == geoModal) {
    closeModal();
  }
};

document.querySelector('.close-geopop')
  .onclick = function() {
    closeModal();
};

document.querySelector('#geoModal #continue-shopping')
  .onclick = function() {
    window.__shopper__ && window.__shopper__.popupCallback && window.__shopper__.popupCallback();
    closeModal();
};
